import React, { useState, useEffect, useCallback } from "react";
import {
  ScatterChart,
  Scatter,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

const CityEventsChart = ({ allLocations, events }) => {
  const [data, setData] = useState([]); //holds data to feed chart

  const getData = useCallback(() => {
    const data = allLocations.map((location) => {
      //maps location array
      const count = events.filter(
        (event) => event.location === location
      ).length; //filters events by each location in resulting array
      const city = location.split(/, | - /)[0]; //uses length value as event count for location
      return { city, count };
    });
    return data;
  }, [allLocations, events]);

  useEffect(() => {
    setData(getData());
  }, [getData]);
  return (
    <ResponsiveContainer
      width="99%"
      height={320}
      className="responsive-container"
    >
      <ScatterChart
        margin={{
          top: 10,
          right: 10,
          botton: 20,
          left: 40,
        }}
      >
        <CartesianGrid />
        <XAxis
          type="category"
          dataKey="city"
          name="City"
          angle={45}
          interval={0}
          tick={{ dx: 20, dy: 40, fontSize: 12 }}
        />
        <YAxis type="number" dataKey="count" name="Number of Events" />
        <Tooltip cursor={{ strokeDasharray: "2 2" }} />
        <Scatter name="scatter" data={data} fill="#8884d8" />
      </ScatterChart>
    </ResponsiveContainer>
  );
};

export default CityEventsChart;
